// В этом файле должны быть стили для БЭМ-блока main-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.main-slider {

  $block-name:                &; // #{$block-name}__element

  @media (min-width: $menu-desktop-width) {
    @include container;
  }

  margin-bottom: 20px;

  &.slick-theme {

    .slick-arrow {
      background: transparent;
      z-index: 2;
    }

    .slick-prev {
      left: 45px;

      &:before {
        background-image: svg-load('../blocks/slick-theme/bg-img/slick-prev-icon.svg', fill=#fff) !important;
      }
    }

    .slick-next {
      right: 45px;

      &:before {
        background-image: svg-load('../blocks/slick-theme/bg-img/slick-next-icon.svg', fill=#fff) !important;
      }
    }
  }

  .slick-dots {
    position: absolute;
    bottom: 20px;
    z-index: 3;

    li {
      button {
        &:before {
          background: #fff;
        }
      }

      &.slick-active {
        button {
          border-color: #fff;
        }
      }
    }
  }

}
